import Passenger from '../passenger/passenger'
export default class FlightOrder { // 
  constructor(details) {
    if (details) {
      for (let key of Object.keys(details)) {
        this[key] = JSON.parse(JSON.stringify(details[key]))
      }
    }
    this.TravelID = details && details.TravelID ? details.TravelID : '' // 
    this.Passengers = details && details.Passengers ? this.setPassenger(details.Passengers) : [] // 
    this.CustomItem = details && details.CustomItem ? details.CustomItem : null // ()
    this.Contacts = details && details.Contacts ? details.Contacts : [] // 
    this.DeliveryInfo = details && details.DeliveryInfo ? details.DeliveryInfo : null // 
    this.PayType = details && details.PayType ? details.PayType : 0 // 
    this.PayStatus = details && details.PayStatus ? details.PayStatus : 0 //  01
    this.Purpose = details && details.Purpose ? details.Purpose : '' // 
    this.AppointVettingPersonID = details && details.AppointVettingPersonID ? details.AppointVettingPersonID : '' // ID
    this.AppointVettingPersonName = details && details.AppointVettingPersonName ? details.AppointVettingPersonName : '' // 
    this.TrvID = details && details.TrvID ? details.TrvID : '' // 
    this.TravelPlansID = details && details.TravelPlansID ? details.TravelPlansID : '' // ID
    this.OriginBookingID = details && details.OriginBookingID ? details.OriginBookingID : null // ID()
    this.OriginPnrID = details && details.OriginPnrID ? details.OriginPnrID : null // pnr ID()
    this.SegmentIDList = details && details.SegmentIDList ? details.SegmentIDList : [] // ID
    this.ViolationFloorPirceReason = details && details.ViolationFloorPirceReason ? details.ViolationFloorPirceReason : '' // 
    this.ViolationCabinRankName = details && details.ViolationCabinRankName ? details.ViolationCabinRankName : '' // 
    this.ViolationCabinRankReason = details && details.ViolationCabinRankReason ? details.ViolationCabinRankReason : '' // 
    this.CompanyPayType = details && details.CompanyPayType ? details.CompanyPayType : '' // 
    this.AuthorizationCode = details && details.AuthorizationCode ? details.AuthorizationCode : '' // 
    this.GoSelectedFlight = details && details.GoSelectedFlight ? details.GoSelectedFlight : {
      FlightNo: '',
      CabinID: '',
      DepartDate: this.beTime(new Date().setDate(new Date().getDate() + 1)),
      FlightDetails: {},
      CabinDetails: {}
    } // 
    this.BackSelectedFlight = details && details.BackSelectedFlight ? details.BackSelectedFlight : {
      FlightNo: '',
      CabinID: '',
      DepartDate: '',
      // DepartDate: this.beTime(new Date().setDate(new Date().getDate() + 2)),
      FlightDetails: {},
      CabinDetails: {}
    } // 
    this.GoLowestPriceFlights = details && details.GoLowestPriceFlights ? details.GoLowestPriceFlights : []// **
    this.BackLowestPriceFlights = details && details.BackLowestPriceFlights ? details.BackLowestPriceFlights : [] // **
    /** ,  */
    this.DepartObject = details && details.DepartObject ? details.DepartObject : '' // 
    this.ArrivalObject = details && details.ArrivalObject ? details.ArrivalObject : '' // 
    /*********************************************************************************************************************************/
    this.DepartCode = details && details.DepartCode ? details.DepartCode : '' // 
    this.ArrivalCode = details && details.ArrivalCode ? details.ArrivalCode : '' // 
    this.DepartName = details && details.DepartName ? details.DepartName : '' // 
    this.ArrivalName = details && details.ArrivalName ? details.ArrivalName : '' // 
    this.DepartCityCode = details && details.DepartCityCode ? details.DepartCityCode : '' // Code
    this.ArrivalCityCode = details && details.ArrivalCityCode ? details.ArrivalCityCode : '' // Code
    this.DepartCityName = details && details.DepartCityName ? details.DepartCityName : '' // 
    this.ArrivalCityName = details && details.ArrivalCityName ? details.ArrivalCityName : '' // 
    this.DepartGeoType = details && details.DepartGeoType ? details.DepartGeoType : '' // (0 1)
    this.ArrivalGeoType = details && details.ArrivalGeoType ? details.ArrivalGeoType : '' // (0 1)
    this.TravelType = details && details.TravelType ? details.TravelType : null // 
    this.TotalPrice = details && details.TotalPrice ? details.TotalPrice : 0 // 
    this.VettingProcessList = details && details.VettingProcessList ? details.VettingProcessList : [] // 
    this.SettlementType = details && details.SettlementType ? details.SettlementType : '' // 
    this.CurrentUserID = details && details.CurrentUserID ? details.CurrentUserID : '' // ID
    this.DisplayConfig = details && details.DisplayConfig ? details.DisplayConfig : {
      IsDisplayCustomItem: false, // 
      IsDisplayPurpose: false, // 
      IsDisplayAuthorizationCode: false, // 
      IsDisplayBusinessUnit: false, // BU
      IsDisplayDepartment: false, // 
      IsDisplayCostCenter: false // 
    } // /
    /** ,  */
    this.PageBtnInfo = details && details.PageBtnInfo ? details.PageBtnInfo : {
      IsAllowCancel: false,
      IsAllowPay: false,
      IsAllowPayAndSubmit: false,
      IsAllowSubmit: false
    }
  }
  beTime(ms, format) {
    let isDate = ms instanceof Date
    let date = ''
    if (isDate) {
      date = new Date(ms)
    } else {
      let val = ms.toString().length === 10 ? ms * 1000 : ms
      date = new Date(val)
    }
    var fmt = format !== undefined ? format : 'yyyy-MM-dd' // yyyy-MM-dd hh:mm
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    let o = {
      'M+': date.getMonth() + 1,
      'd+': date.getDate(),
      'h+': date.getHours(),
      'm+': date.getMinutes(),
      's+': date.getSeconds()
    }
    for (let k in o) {
      if (new RegExp(`(${k})`).test(fmt)) {
        let str = o[k] + ''
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : ('00' + str).substr(str.length))
      }
    }
    return fmt
  }
  setPassenger(list) {
    return list.forEach((element, index) => {
      list[index] = new Passenger(element)
    })
  }
}



