export default class IntlFlightOrder { // 
  constructor(details) {
    if (details) {
      for (let key of Object.keys(details)) {
        this[key] = JSON.parse(JSON.stringify(details[key]))
      }
    }
    this.SearchParams = details && details.SearchParams ? details.SearchParams : {
      Segments: [{
        DepartObject: {}, // , 
        ArrivalObject: {}, // , 
        DepartureTime: this.beTime(new Date().setDate(new Date().getDate() + 1)),
        DepartureAirportCode: '',
        ArrivalAirPortCode: ''
      }],
      DirectFlight: false,
      FlightClass: '',
      AdultCount: 1,
      ChildCount: 0,
      InfantCount: 0,
      AirLine: ''
    } // 
    this.SelectedSegmentIDs = details && details.SelectedSegmentIDs ? details.SelectedSegmentIDs : '' // ID
    this.SelectedFareInfoID = details && details.SelectedFareInfoID ? details.SelectedFareInfoID : '' // ID
    this.TravelType = details && details.TravelType ? details.TravelType : '' //  0: 1:
    this.AuthorizationCode = details && details.AuthorizationCode ? details.AuthorizationCode : '' // 
    this.Purpose = details && details.Purpose ? details.Purpose : '' // 
    this.Passengers = details && details.Passengers ? details.Passengers : [] // 
    this.CustomItem = details && details.CustomItem ? details.CustomItem : {} // ()
    this.Contacts = details && details.Contacts ? details.Contacts : [] // 
    this.PayType = details && details.PayType ? details.PayType : null // 
    this.AppointVettingPersonID = details && details.AppointVettingPersonID ? details.AppointVettingPersonID : '' // ID
    this.AppointVettingPersonName = details && details.AppointVettingPersonName ? details.AppointVettingPersonName : '' // 
    this.TotalPrice = details && details.TotalPrice ? details.TotalPrice : 0 // 
    /** ,  */
    this.RequestSegmentIndex = details && details.RequestSegmentIndex ? details.RequestSegmentIndex : 1
    this.SelectedSegment = details && details.SelectedSegment ? details.SelectedSegment : []
    this.PageBtnInfo = details && details.PageBtnInfo ? details.PageBtnInfo : {
      IsAllowCancel: false,
      IsAllowPay: false,
      IsAllowPayAndSubmit: false,
      IsAllowSubmit: false
    }
  }
  beTime(ms, format) {
    let isDate = ms instanceof Date
    let date = ''
    if (isDate) {
      date = new Date(ms)
    } else {
      let val = ms.toString().length === 10 ? ms * 1000 : ms
      date = new Date(val)
    }
    var fmt = format !== undefined ? format : 'yyyy-MM-dd' // yyyy-MM-dd hh:mm
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    let o = {
      'M+': date.getMonth() + 1,
      'd+': date.getDate(),
      'h+': date.getHours(),
      'm+': date.getMinutes(),
      's+': date.getSeconds()
    }
    for (let k in o) {
      if (new RegExp(`(${k})`).test(fmt)) {
        let str = o[k] + ''
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : ('00' + str).substr(str.length))
      }
    }
    return fmt
  }
}



