// Config API https://www.kancloud.cn/yunye/axios/234845
const BASE_URL = '/api'
function getHeaders() {
  return {}
}

// POSTFILE
export function postFile (url, file, filePath) {
  let RequestSource = 'Web' // Wechat:  WechatMiniProgram:  Web:  Android:  Apple: ios
  let ua = window.navigator.userAgent.toLowerCase() // 
  if (window.__wxjs_environment === 'miniprogram') {
    // RequestSource = 'WechatMiniProgram'
    RequestSource = 'Wechat'
  } else if (/micromessenger/.test(ua)) {
    RequestSource = 'Wechat'
  }
  return {
    url: url,
    method: 'post', //  get
    baseURL: BASE_URL,
    // `transformRequest` 
    //  'PUT', 'POST'  'PATCH' 
    //  ArrayBuffer Stream
    transformRequest: [function (data) {
      //  data 
      // return data
      return data
    }],
    // `transformResponse`  then/catch 
    transformResponse: [function (data) {
      //  data 
      return data
    }],
    headers: {
      'Accept': 'application/json',
      'RequestSource': encodeURI(RequestSource),
      'SoftwareVersion': encodeURI(window.localStorage.version),
      'DeviceInformation': encodeURI(ua),
      'LanguageType': window.localStorage.language === 'CN' ? 1 : 2
    },
    // `params`  URL 
    // (plain object) URLSearchParams 
    params: Object.assign(getHeaders(), {filePath: encodeURIComponent(`${filePath}/`)}),
    // `data` 
    //  'PUT', 'POST',  'PATCH'
    //  `transformRequest` 
    // - string, plain object, ArrayBuffer, ArrayBufferView, URLSearchParams
    // - FormData, File, Blob
    // - Node  Stream
    data: file,
    // `timeout` (0 )
    //  `timeout` 
    timeout: 120000,
    // `withCredentials` 
    withCredentials: true, // 
    // `responseType`  'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
    responseType: 'json', // 
    // `xsrfCookieName`  xsrf token cookie
    xsrfCookieName: 'XSRF-TOKEN', // default
    // `xsrfHeaderName`  xsrf token  HTTP 
    xsrfHeaderName: 'X-XSRF-TOKEN', // 
    // `onUploadProgress` 
    // onUploadProgress: function (progressEvent) {
    //   // 
    // },
    // // `onDownloadProgress` 
    // onDownloadProgress: function (progressEvent) {
    //   // 
    // },
    // `maxContentLength` 
    // maxContentLength: 2000,
    // `validateStatus` HTTP  resolve  reject  promise  `validateStatus`  `true` ( `null`  `undefined`)promise  resolve; promise  rejecte
    validateStatus: function (status) {
      return status >= 200 && status < 300 // 
    },
    // `maxRedirects`  node.js  follow 
    // 0 follow 
    maxRedirects: 5 // 
  }
}

// POST
export function commonRequest (url, method, params, data, cancelToken) {
  let RequestSource = 'Web' // Wechat:  WechatMiniProgram:  Web:  Android:  Apple: ios
  let ua = window.navigator.userAgent.toLowerCase() // 
  if (window.__wxjs_environment === 'miniprogram') {
    // RequestSource = 'WechatMiniProgram'
    RequestSource = 'Wechat'
  } else if (/micromessenger/.test(ua)) {
    RequestSource = 'Wechat'
  }
  return {
    // `url`  URL
    url: url,
    // `method` 
    method: method, //  get
    // `baseURL`  `url`  `url`  URL
    //  `baseURL`  axios  URL
    baseURL: BASE_URL,
    // `transformRequest` 
    //  'PUT', 'POST'  'PATCH' 
    //  ArrayBuffer Stream
    transformRequest: [function (data) {
      //  data 
      // return data
      return JSON.stringify(data)
    }],
    // `transformResponse`  then/catch 
    transformResponse: [function (data) {
      //  data 
      return (typeof (data) === 'string') ? JSON.parse(data) : data
    }],
    headers: {
      'Accept': 'application/json;charset=UTF-8',
      'Content-Type': 'application/json',
      'RequestSource': encodeURI(RequestSource),
      'SoftwareVersion': encodeURI(window.localStorage.version),
      'DeviceInformation': encodeURI(ua),
      'LanguageType': window.localStorage.language === 'CN' ? 1 : 2
    },
    // `params`  URL 
    // (plain object) URLSearchParams 
    params: Object.assign(getHeaders(), params),
    // params: params,
    // `data` 
    //  'PUT', 'POST',  'PATCH'
    //  `transformRequest` 
    // - string, plain object, ArrayBuffer, ArrayBufferView, URLSearchParams
    // - FormData, File, Blob
    // - Node  Stream
    data: data,
    // `timeout` (0 )
    //  `timeout` 
    timeout: 60000,
    // `withCredentials` 
    withCredentials: true, // false
    // `responseType`  'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
    responseType: 'json', // 
    // `xsrfCookieName`  xsrf token cookie
    xsrfCookieName: 'XSRF-TOKEN', // default
    // `xsrfHeaderName`  xsrf token  HTTP 
    xsrfHeaderName: 'X-XSRF-TOKEN', // 
    // `onUploadProgress` 
    onUploadProgress: function (progressEvent) {
      // 
    },
    // `onDownloadProgress` 
    onDownloadProgress: function (progressEvent) {
      // 
    },
    // `maxContentLength` 
    maxContentLength: 2000,
    // `validateStatus` HTTP  resolve  reject  promise  `validateStatus`  `true` ( `null`  `undefined`)promise  resolve; promise  rejecte
    validateStatus: function (status) {
      return status >= 200 && status < 300 // 
    },
    // `maxRedirects`  node.js  follow 
    // 0 follow 
    maxRedirects: 5, // 
    cancelToken: cancelToken
  }
}



