export default class trainOrder {
  constructor (details) {
    if (details) {
      for (let key of Object.keys(details)) {
        this[key] = JSON.parse(JSON.stringify(details[key]))
      }
    }
    this.OriginalOrderID = details && details.OriginalOrderID ? details.OriginalOrderID : null // ID
    this.TravelType = details && details.TravelType ? details.TravelType : null // 
    this.DepartStationCode = details && details.DepartStationCode ? details.DepartStationCode : '' // Code
    this.DepartStationName = details && details.DepartStationName ? details.DepartStationName : '' // 
    this.ArrivalStationCode = details && details.ArrivalStationCode ? details.ArrivalStationCode : '' // Code
    this.ArrivalStationName = details && details.ArrivalStationName ? details.ArrivalStationName : '' // 
    this.DepartDate = this.beTime(new Date().setDate(new Date().getDate() + 1));
    this.ArrivalDate = ''
    // this.ArrivalDate = this.beTime(new Date().setDate(new Date().getDate() + 2));
    this.isHighSpeed = false;
    this.GoSelectedRoute = details && details.GoSelectedRoute ? details.GoSelectedRoute : {TrainCode: ''};
    this.BackSelectedRoute = details && details.BackSelectedRoute ? details.BackSelectedRoute : {TrainCode: ''};
    // book
    this.Passengers = details && details.Passengers ? details.Passengers : [] // 
    this.CustomItem = details && details.CustomItem ? details.CustomItem : {} // ()
    this.Contacts = details && details.Contacts ? details.Contacts : [] // 
    this.PayType = details && details.PayType ? details.PayType : null // 
    this.Purpose = details && details.Purpose ? details.Purpose : '' // 
    this.AuthorizationCode = details && details.AuthorizationCode ? details.AuthorizationCode : '' // 
    this.AppointVettingPersonID = details && details.AppointVettingPersonID ? details.AppointVettingPersonID : '' // ID
    this.AppointVettingPersonName = details && details.AppointVettingPersonName ? details.AppointVettingPersonName : '' // 
    this.ViolationRankName = details && details.ViolationRankName ? details.ViolationRankName : '' // 
    this.ViolationRankReason = details && details.ViolationRankReason ? details.ViolationRankReason : '' // 
    this.VettingProcessList = details && details.VettingProcessList ? details.VettingProcessList : [] // 
    this.IsAcptStanding = details && details.IsAcptStanding ? details.IsAcptStanding : false
    this.DeliveryType = details && details.DeliveryType ? details.DeliveryType : 3
    this.DeliveryAddress = details && details.DeliveryAddress ? details.DeliveryAddress : ''
    /** ,  */
    this.DepartObject = details && details.DepartObject ? details.DepartObject : '' // 
    this.ArrivalObject = details && details.ArrivalObject ? details.ArrivalObject : '' // 
    this.Ids = details && details.Ids ? details.Ids : []
    /*******************************************************************************************************************************************************/
  }
  beTime(ms, format) {
    let isDate = ms instanceof Date
    let date = ''
    if (isDate) {
      date = new Date(ms)
    } else {
      let val = ms.toString().length === 10 ? ms * 1000 : ms
      date = new Date(val)
    }
    var fmt = format !== undefined ? format : 'yyyy-MM-dd' // yyyy-MM-dd hh:mm
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    let o = {
      'M+': date.getMonth() + 1,
      'd+': date.getDate(),
      'h+': date.getHours(),
      'm+': date.getMinutes(),
      's+': date.getSeconds()
    }
    for (let k in o) {
      if (new RegExp(`(${k})`).test(fmt)) {
        let str = o[k] + ''
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : ('00' + str).substr(str.length))
      }
    }
    return fmt
  }
}



