import Vue from 'vue'
export default class Passenger { //  + 
  constructor(details) {
    // 
    this.ID = details && details.ID ? details.ID : (details && details.UpID ? details.UpID : '') // ID
    this.Credentials = details && details.Credentials ? this.fillInCredentials(details.Credentials) : this.fillInCredentials() // 
    this.Gender = (details === undefined || details.Gender === undefined || details.Gender === null) ? true : details.Gender //  true
    this.Nation = details && details.Nation ? details.Nation : null // 
    this.NationCode = details && details.NationCode ? details.NationCode : '' // 
    this.NationName = details && details.NationName ? details.NationName : '' // 
    // ====================================================================================================
    // 
    this.UpID = details && details.UpID ? details.UpID : (details && details.ID ? details.ID : '') // ID  this.ID
    this.Credential = details && details.Credential ? this.initCredential(details.Credential) : this.initCredential() // 
    this.IsSendBookSms = details && details.IsSendBookSms ? details.IsSendBookSms : false // 
    this.IsSendBookEmail = details && details.IsSendBookEmail ? details.IsSendBookEmail : false // 
    this.IsSendIssuedSms = details && details.IsSendIssuedSms ? details.IsSendIssuedSms : false // 
    this.IsSendIssuedEmail = details && details.IsSendIssuedEmail ? details.IsSendIssuedEmail : false // 
    this.IsSendBookWechat = details && details.IsSendBookWechat ? details.IsSendBookWechat : false // 
    this.IsSendIssuedWechat = details && details.IsSendIssuedWechat ? details.IsSendIssuedWechat : false // 
    this.IsSendConfirmSms = details && details.IsSendConfirmSms ? details.IsSendConfirmSms : false // 
    this.IsSendConfirmEmail = details && details.IsSendConfirmEmail ? details.IsSendConfirmEmail : false // 
    this.Insurances = details && details.Insurances ? details.Insurances : [] // 
    this.openDelete = details && details.openDelete ? details.openDelete : false // 
    // this.isFrequentContact = details && details.isFrequentContact ? details.isFrequentContact : false // 
    // ====================================================================================================
    // 
    this.UpType = details && details.UpType ? details.UpType : 2 // (UPNUP)
    this.Name = details && details.Name ? details.Name : '' // ()
    this.ChName = details && details.ChName ? details.ChName : '' // 
    this.ENnameFirst = details && details.ENnameFirst ? details.ENnameFirst : '' // 
    this.ENnameMiddle = details && details.ENnameMiddle ? details.ENnameMiddle : '' // 
    this.ENnameLast = details && details.ENnameLast ? details.ENnameLast : '' // 
    this.Birthday = details && details.Birthday ? details.Birthday : '' // 
    this.Email = details && details.Email ? details.Email : '' // 
    this.Mobile = details && details.Mobile ? details.Mobile : '' // 
    this.Department = details && details.Department ? details.Department : null // 
    this.CostCenter = details && details.CostCenter ? details.CostCenter : null // 
    this.BusinessUnit = details && details.BusinessUnit ? details.BusinessUnit : null // BU
    this.UPRole = details && details.UPRole ? details.UPRole : '' // 
    this.PersonType = details && details.PersonType ? details.PersonType : '' //  
    this.PassengerType = details && details.PassengerType ? details.PassengerType : 1 // , 
    this.TravelType = details && details.TravelType ? details.TravelType : 0 // 
    this.TravelerEntry = details && details.TravelerEntry ? details.TravelerEntry : 0 // 0;1
    // this.roomIndex = details && details.roomIndex ? details.roomIndex : -1 // 
    this.GuestToRoomNo = details && details.GuestToRoomNo >= 0 ? details.GuestToRoomNo : -1 // 
    this.GuestType = details && details.GuestType ? details.GuestType : 0
    this.TravelCardList = details && details.TravelCardList ? details.TravelCardList : [] // 
  }
  fillInCredentials(currentValue = []) {
    let res = [
      {ID: '', CredentialType: 1, CredentialName: Vue.prototype.$t('base.IDTypeNames.IDCard'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 2, CredentialName: Vue.prototype.$t('base.IDTypeNames.Passport'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 3, CredentialName: Vue.prototype.$t('base.IDTypeNames.StudentCard'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 4, CredentialName: Vue.prototype.$t('base.IDTypeNames.MilitaryID'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 5, CredentialName: Vue.prototype.$t('base.IDTypeNames.HomeReturnPermit'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 6, CredentialName: Vue.prototype.$t('base.IDTypeNames.ForeignersPermanentResidencePermit'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 7, CredentialName: Vue.prototype.$t('base.IDTypeNames.EntryExitPassForHongKongAndMacau'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 8, CredentialName: Vue.prototype.$t('base.IDTypeNames.EntryExitPassForTaiwan'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 9, CredentialName: Vue.prototype.$t('base.IDTypeNames.InternationalSeafarersCard'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 10, CredentialName: Vue.prototype.$t('base.IDTypeNames.TaiwanCompatriotPermit'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 11, CredentialName: Vue.prototype.$t('base.IDTypeNames.Others'), CredentialNo: '', EffectiveDate: '', IsDefault: false},
      {ID: '', CredentialType: 12, CredentialName: Vue.prototype.$t('base.IDTypeNames.HongKongMacaoAndTaiwanResidentsResidencePermit'), CredentialNo: '', EffectiveDate: '', IsDefault: false}
    ]
    // --
    res = [this.Credential, ...currentValue, ...res].filter(v => v)
    let maplist = new Map()
    res = res.filter(a => !maplist.has(a.CredentialType) && maplist.set(a.CredentialType, 1))
    res = res.sort((a, b) => { return a['CredentialType'] - b['CredentialType'] })
    // let maplist = []
    // res = res.filter(a => !(maplist.indexOf(a.CredentialType) >= 0) && maplist.push(a.CredentialType))
    return res
  }
  initCredential(currentValue) {
    let list = [currentValue, ...this.Credentials].filter(v => v)
    let CredentialNos = list.map(v => v.CredentialNo).filter(v => v)
    let index = 0
    if (CredentialNos.length > 0) {
      index = list.map(v => v.CredentialNo).indexOf(CredentialNos[0])
    }
    return list[index]
  }
}



