<template>
  <div id="app">
    <div ref="fixPositionBugInIphone7"></div>
    <!-- <transition :name="`vux-pop-${direction === 'forward' ? 'in' : 'out'}`"> -->
    <router-view/>
    <!-- </transition> -->
  </div>
</template>

<script type="text/ecmascript-6">
import {mapGetters} from 'vuex'
export default {
  name: 'App',
  computed: {
    ...mapGetters([
      'direction'
    ])
  },
  mounted() {
    window.onresize = () => {
      this.scroll && this.scroll.forceUpdate()
    }
    /**
     * fixed, fixedabsolute, fixed
     * iphone7,,
     * ,positionfixed<=>absolute
     * ,fixPositionBugInIphone7 DIV,, DIV, 
     * ,
     */
    document.body.addEventListener('focusin', () => {
      this.$refs.fixPositionBugInIphone7.style.height = '1px'
      document.body.style.position = 'absolute'
    })
    document.body.addEventListener('focusout', () => {
      this.$refs.fixPositionBugInIphone7.style.height = '0px'
      document.body.style.position = 'fixed'
      document.body.style.bottom = '0'
    })
    // ================================================================
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
#app
  width 100%
  height 100%
</style>



