export default class HotelOrder { // 
  constructor(details) {
    if (details) {
      for (let key of Object.keys(details)) {
        this[key] = JSON.parse(JSON.stringify(details[key]))
      }
    }
    this.OrderID = details && details.OrderID ? details.OrderID : null // ID
    this.CityName = details && details.CityName ? details.CityName : '' // 
    this.CityID = details && details.CityID ? details.CityID : '' // ID
    this.HotelID = details && details.HotelID ? details.HotelID : '' // ID
    this.CheckInDate = details && details.CheckInDate ? details.CheckInDate : this.beTime(new Date()) // 
    this.CheckOutDate = details && details.CheckOutDate ? details.CheckOutDate : this.beTime(new Date().setDate(new Date().getDate() + 1)) // 
    this.RoomID = details && details.RoomID ? details.RoomID : '' // ID
    this.RatePlanID = details && details.RatePlanID ? details.RatePlanID : '' // ID
    this.RoomAmount = details && details.RoomAmount ? details.RoomAmount : 1 // 
    this.CustomItem = details && details.CustomItem ? details.CustomItem : {} // ()
    this.Contacts = details && details.Contacts ? details.Contacts : [] // 
    this.PayType = details && details.PayType ? details.PayType : null // 
    this.Purpose = details && details.Purpose ? details.Purpose : '' // 
    this.AuthorizationCode = details && details.AuthorizationCode ? details.AuthorizationCode : '' // 
    this.AppointVettingPersonID = details && details.AppointVettingPersonID ? details.AppointVettingPersonID : '' // ID
    this.AppointVettingPersonName = details && details.AppointVettingPersonName ? details.AppointVettingPersonName : '' // 
    this.ViolationRankName = details && details.ViolationRankName ? details.ViolationRankName : '' // 
    this.ViolationRankReason = details && details.ViolationRankReason ? details.ViolationRankReason : '' // 
    this.Guests = details && details.Guests ? details.Guests : [] // 
    this.ArrivalTime = details && details.ArrivalTime ? details.ArrivalTime : '18:00' // (HHmm)
    this.SpecificRequirements = details && details.SpecificRequirements ? details.SpecificRequirements : '' // 
    this.TravelType = details && details.TravelType ? details.TravelType : null // 
    this.OrderType = details && details.OrderType ? details.OrderType : null // 
    this.VettingProcessList = details && details.VettingProcessList ? details.VettingProcessList : [] // 
    this.filters = details && details.filters ? details.filters : [] // 
    // 
    this.AdultAmount = details && details.AdultAmount ? details.AdultAmount : 0
    this.NationalityName = details && details.NationalityName ? details.NationalityName : ''
    this.NationalityCode = details && details.NationalityCode ? details.NationalityCode : 'CN'
    /** ,  */
    this.City = details && details.City ? details.City : null
    this.Hotel = details && details.Hotel ? details.Hotel : null
    /*******************************************************************************************************************************************************/
  }
  beTime(ms, format) {
    let isDate = ms instanceof Date
    let date = ''
    if (isDate) {
      date = new Date(ms)
    } else {
      let val = ms.toString().length === 10 ? ms * 1000 : ms
      date = new Date(val)
    }
    var fmt = format !== undefined ? format : 'yyyy-MM-dd' // yyyy-MM-dd hh:mm
    if (/(y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
    }
    let o = {
      'M+': date.getMonth() + 1,
      'd+': date.getDate(),
      'h+': date.getHours(),
      'm+': date.getMinutes(),
      's+': date.getSeconds()
    }
    for (let k in o) {
      if (new RegExp(`(${k})`).test(fmt)) {
        let str = o[k] + ''
        fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : ('00' + str).substr(str.length))
      }
    }
    return fmt
  }
}



